<template>
<!--  eslint-disable-next-line -->
<!--  eslint-disable -->
 <main class="mt-3">
    <div class="container">
      <button type="button" class="btn btn-dark">
        <router-link class="navbar-brand" to="/management"></router-link>
      </button>
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          <input type="text" class="form-control" v-model="product.product_name">
        </div>
      </div>

      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"> </label>
        <div class="col-md-9">
          <input type="text" class="form-control" v-model="product.product_subtitle">
        </div>
      </div>
      
      <!--   -->
      <div class="mb-3 row">
          <label class="col-md-3 col-form-label"></label>
          <div class="col-md-9">
            <select class="form-select mb-4 text-end" aria-label="Default select example"  v-model="product.category_id">
              <option selected value="0"></option>
              <option value="1">  >> </option>
              <option value="2">  >> </option>
              <option value="3">   >> +</option>
            </select>
          </div>
      </div>

      <!--  -->
      <div class="mb-3 row">
        <label class="col-md-3 col-form-label"></label>
        <div class="col-md-9">
          <input type="text" class="form-control" v-model="product.tags">
        </div>
      </div>
    
    <!--  -->
    <div class="mb-3 row">
        <div class="col-6 d-grid p-1">
            <button type="button" class="btn btn-dark" @click="goToList"></button>
        </div>
        <div class="col-6 d-grid p-1">
            <button type="button" class="btn btn-danger" @click="productInsert"></button>
        </div>
    </div>
    </div>
  </main>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */

export default {
  data() {
    return {
      productId: 0,
      product: { // v-model
        product_name: "",
        product_subtitle: "",
        tags: "",
        category_id: 0,
        seller_id: 1
      }
    };
  },
    created() {
    this.productId = this.$route.query.product_id;
    console.log('this.productId',this.productId)
  },
    computed: {
    user(){
      return this.$store.state.user;
    }
  },
  //   watch: {
  //   productId: function(newProductId) {
  //     this.prices.product_id = newProductId;
  //   }
  // },
  methods: {
    goToList() {
        this.$router.push({path:'/management'});
    },
    productInsert(){    
    if (this.product.product_name == ""){
      return this.$swal("  ");
    } 
    if (this.product.product_subtitle == ""){
      return this.$swal("  ");
    }
     if (this.product.tags == ""){
      return this.$swal("  ");
    }
    this.$swal.fire({
      title: '  ?',
      showCancelButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
      }).then(async (result) => {
      if(result.isConfirmed){
        await this.$api('/api/productInsert', {param:[this.product]});
        console.log(this.product);
        this.$swal.fire('!', '', 'success');
        this.$router.push({path:'/management'});                             
        }
    });
  },
  }
};
</script>