<template>
    <div class="d-flex justify-content-between align-items-center card-text pt-3 border-top pad">
        <div class="row">
            <div class="col-auto">
                <font-awesome-icon :icon="['fas', 'cart-shopping']" class="fa-sm" />
                <label class="col-form-label"></label>
                <p style="text-align: left;">  10</p>
            </div>
            <!--  
             input   -->
            <div class="col-auto">
              <div class="input-group">
                <span class="input-group-text" style="cursor:pointer;" @click="minus()">-</span>
                <input type="text" class="form-control" style="width:40px;" v-model="count">
                <span class="input-group-text" style="cursor:pointer;" @click="plus()">+</span>
              </div>  
            </div>
         </div>
         <div class="row pt-3 pb-3">
                <div style="text-align: right;">
                   <p>: {{ totalPrice }}</p>
                </div>
         </div>
    </div>
</template>

<script>
// eslint- disable-next-line
/* eslint-disable */

export default {
    data() {
      return {
        count: 1, //
        totalPrice: 0, //
        selectedPrice: 0, //      
        click01: false,
        click02: false,
        click03: false
      }
    },
    computed: { //   computed
      total(){
        return this.selectedPrice * this.count;
      }
    },
    watch: { //       watch
      // selectedPrice   total  
      selectedPrice: function (newPrice, oldPrice) {
          this.count = 1; //    count 1 
      },
      total(newTotal) {
        this.$emit('updateTotal', newTotal);
      }
    },
    methods: {
      click1() {
        this.stateId = 0;
        this.click01 = true;
        this.click02 = false;
        this.click03 = false;
      },
      click2() {
        this.stateId = 0;
        this.click01 = false;
        this.click02 = true;
        this.click03 = false;
      },
      click3() {
        this.stateId = 0;
        this.click01 = false;
        this.click02 = false;
        this.click03 = true;
      },
      plus() {
      if( this.count < 10 ) {
        this.count = this.count + 1;	
        //this.totalPrice = (this.selectedPrice * this.count);
      } else {
        alert("   10 .");
      }
      this.totalPrice = this.selectedPrice * this.count;
      this.$emit('updateTotal', this.totalPrice);
      this.updateSelectedPrice(this.selectedPrice); //   
    },
    minus() {
      if(this.count <= 1) {
        return;
        }else {
          this.count = this.count - 1;
          this.totalPrice = this.selectedPrice * this.count;
          this.$emit('updateTotal', this.totalPrice);
       }
    },
      increment(){
        this.$store.commit('increment');
      },
      updateSizePrice(sizePrice) {
      this.selectedPrice = sizePrice;
      // this.$emit('updateTotal', this.total);
      // console.log('this.total', this.total)
    },
     updateSelectedPrice(selectedPrice) {
    this.$emit('updateSelectedPrice', selectedPrice);
  },
  },
    mounted() {
      this.emitter.on("selectedPrice", (a) => {
        console.log(' '+ a);
        this.selectedPrice = a;
        this.$emit('updateTotal', this.total);
        //this.updateSizePrice(a); // 
        console.log('this.total', this.total)
      });
    }
};
</script>

<style scoped>

.btn-clicked { /*  */
  border: solid 2px #1C2474;
  border-radius: 5px;
  padding: 8px;
  background-color:#fde7e4;
  color:#1C2474;
}

.btnn { /*  */
  border: solid 1px #1C2474;
  border-radius: 5px;
  padding: 8px;
  background-color:#1C2474;
  color:#ffffff;
}

</style>