/* eslint-disable */

import { createStore } from 'vuex'
import persistedstate from 'vuex-persistedstate' // persistedstate  

const store = createStore({
  state() {
    return {
      user: {} //   
    }
  },
  mutations: {
    user(state, data){
      state.user = data;
    }
  },
  plugin: [
    persistedstate({ //  
      paths: ['user'] //  state 
    })
  ]
});

export default store;